import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col'; 
import './CarListing.css';
import CarImg from '../../Assets/Img/car101.jpg';
import CarImg2 from '../../Assets/Img/car101.png';
import CarImg3 from '../../Assets/Img/car103.png';
import { CarRental, CarRentalOutlined, GasMeter, SearchOutlined, TrendingUp } from '@mui/icons-material'; 
import { FaCarAlt, FaGasPump } from 'react-icons/fa';
import Cars from './Cars';

const CarListing = () => {
  return (
    <div className='CarListing'>
        <Container className='container'>
            <Row>
                <Col xs={12} md={4}>
                    <form className="search-box">
                        <Row>
                            <Col xs={12} md={12}>
                                <h2>Search Car Deals <TrendingUp /></h2>
                            </Col>
                            <Col xs={12} md={12}>
                                <label htmlFor="destination">Pickup / Drop-off Location </label>
                                <div class="input-group mb-3">
                                    <span class="input-group-text" id="destination"><CarRentalOutlined /> </span>
                                    <input type="text" class="form-control" placeholder='Bodrum - Milas Airport international (BJV)'  aria-label="Destination" aria-describedby="destination" />
                                </div> 
                            </Col>
                            <Col xs={12} md={7}>
                                <label htmlFor="pickupdate">Pick up date</label>
                                <div class="mb-3"> 
                                    <input type="date" class="form-control" aria-label="Pickupdate" aria-describedby="pickupdate" />
                                </div> 
                            </Col>
                            <Col xs={12} md={5}>
                                <label htmlFor="pickuptime">Pick up time</label>
                                <div class="mb-3"> 
                                    <input type="time" class="form-control" aria-label="Pickuptime" aria-describedby="pickuptime" />
                                </div> 
                            </Col>
                            <Col xs={12} md={7}>
                                <label htmlFor="dropoffdate">Drop off date</label>
                                <div class="mb-3"> 
                                    <input type="date" class="form-control" aria-label="Dropoffdate" aria-describedby="dropoffdate" />
                                </div> 
                            </Col>
                            <Col xs={12} md={5}>
                                <label htmlFor="dropofftime">Drop off time</label>
                                <div class="mb-3"> 
                                    <input type="time" class="form-control" aria-label="Dropofftime" aria-describedby="dropofftime" />
                                </div> 
                            </Col>
                            <Col xs={12} md={6}>
                                <label htmlFor="vehicleclass">Vehicle Class</label>
                                <div class="input-group mb-3">
                                    <span class="input-group-text" id="vehicleclass"><FaCarAlt /> </span>
                                    <select class="form-select" aria-label="Vehicleclass" aria-describedby="vehicleclass">
                                        <option value={'Economy'}>Economy</option>
                                        <option value={'Medium'}>Medium</option>
                                        <option value={'Luxury '}>Luxury </option>
                                        <option value={'SUV'}>SUV </option>
                                        <option value={'7+ Seats'}>7+ Seats </option>
                                    </select>
                                </div> 
                            </Col>
                            <Col xs={12} md={6}>
                                <label htmlFor="fueltype">Fuel Type</label>
                                <div class="input-group mb-3">
                                    <span class="input-group-text" id="fueltype"><FaGasPump /> </span>
                                    <select class="form-select" aria-label="Fueltype" aria-describedby="fueltype">
                                        <option value={'Petrol'}>Petrol</option>
                                        <option value={'Diesel'}>Diesel</option>
                                    </select>
                                </div> 
                            </Col>
                            <Col xs={12} md={6}>
                                <label htmlFor="transmission">Transmission</label>
                                <div class="input-group mb-3">
                                    <span class="input-group-text" id="transmission"><GasMeter /> </span>
                                    <select class="form-select" aria-label="transmission" aria-describedby="transmission">
                                        <option value={'Manual'}>Manual</option>
                                        <option value={'Automatic'}>Automatic</option>
                                    </select>
                                </div> 
                            </Col>
                            <Col xs={12} md={6}>
                                <div className="btn btn-primary mt-5"> Find Deals <SearchOutlined /></div>
                            </Col>
                        </Row>
                    </form>
                </Col>
                <Col xs={12} md={8}>
                    <Row>
                        <Col xs={12} md={12}>
                            <h2 style={{textAlign: 'right'}}><CarRental /> Car Listing</h2>
                            <Cars
                                carTitle = 'Ford Edge (2013)'
                                img ={CarImg}
                                vehicleClass = 'Medium'
                                status='not booked'
                                fuel = 'Petrol'
                                transmission = 'Automatic'
                                persons={5}
                                days={6}
                                amount='NGN120,000'
                                daily='NGN20,000'
                                id={1}
                            />
                             
                            <Cars
                                carTitle = 'Citoen Elysee Diesel Manual'
                                img ={CarImg2}
                                vehicleClass = 'Economy'
                                status='booked'
                                fuel = 'Diesel'
                                transmission = 'Manual'
                                persons={4}
                                days={4}
                                amount='NGN80,000'
                                daily='NGN20,000'
                                id={2}
                            />

                            <Cars
                                carTitle = 'Fiat Egea Petrol Manual'
                                img ={CarImg3}
                                vehicleClass = 'Luxury'
                                status='not booked'
                                fuel = 'Petrol'
                                transmission = 'Manual'
                                persons={4}
                                days={2}
                                amount='NGN130,000'
                                daily='NGN65,000'
                                id={3}
                            />
                        </Col>
                    </Row>
                </Col>
            </Row>
        </Container>

    </div>
  )
}

export default CarListing