import React from 'react'
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import './TourBooking.css';
import Park101 from '../../Assets/Img/parkImg.png';
import { FaSignInAlt } from 'react-icons/fa';
import { Link } from 'react-router-dom/cjs/react-router-dom';
import { PaymentOutlined } from '@mui/icons-material';

const TourBooking = () => {
  return (
    <div className='TourBooking'>
        <Container className='container'>
            <Row>
                <Col xs={12} md={4}>
                    <Row>
                        <Col xs={12} md={8}>
                            <div className="circle-box">
                                <p>Step 1</p>
                                <h5>Select Tour</h5>
                            </div>
                            
                        </Col>
                        <Col xs={12} md={8}>
                            <div className="circle-box">
                                <p>Step 2</p>
                                <h5>Your  Details</h5>
                            </div>
                            
                        </Col>
                        <Col xs={12} md={8}>
                            <div className="circle-box">
                                <p>Step 3</p>
                                <h5>Finish Reservation</h5>
                            </div>
                            
                        </Col>

                    </Row>
                </Col>
                <Col xs={12} md={8}>
                    <Row>
                        <Col xs={12} md={12}>
                            <h2>Your Selection</h2>
                            <p>Booking Information</p>
                        </Col>
                         
                        
                        <Col xs={12} md={6}>
                            <img src={Park101} alt="booking-img" className='img-fluid' />
                        </Col>
                        <Col xs={12} md={6} className='booking-details'>
                            <Row>
                                <Col xs={12} md={12}>
                                    <h2>National Museum Abuja</h2>
                                    <p>Abuja, <b>Nigeira</b></p>
                                    <h4>Booking Details:</h4>
                                </Col>
                                <Col xs={12} md={6}>
                                    <span><b>Check In: </b></span>
                                </Col>
                                <Col xs={12} md={6}>
                                    <span><b>Mon, Sep 10, 2023 </b></span>
                                    (11:00AM)
                                </Col>
                                <Col xs={12} md={6}>
                                    <span><b>Check Out: </b></span>
                                </Col>
                                <Col xs={12} md={6}>
                                    <span><b>Mon, Sep 10, 2023 </b></span>
                                    (12:00 PM)
                                </Col>
                                <Col xs={12} md={6}>
                                    <span><b>Total length of stay:  </b></span>
                                </Col>
                                <Col xs={12} md={6}>
                                    <p>1 hour</p>
                                </Col> 
                                <Col xs={12} md={6}>
                                    <span><b>You selected: </b></span>
                                </Col>
                                <Col xs={12} md={6}>
                                    <p>Face Treatment</p>
                                </Col>
                            </Row>
                            
                        </Col>

                          
                        
                    </Row>

                    <Row>
                        <Col xs={12} md={12}>
                            <h2>Your Details</h2>
                            <p>Personal Information</p>
                        </Col>
                        <Col xs={12} md={12}>
                            <div className='login-center'>
                                <button className="btnMore">Sign In <FaSignInAlt /></button>
                                <p>-------------------- &nbsp; Or &nbsp; --------------------</p>
                            </div>
                        </Col>
                    </Row>

                    <Row>
                        <form className='booking-form-1'>
                            <Row>
                                <Col xs={12} md={6}>
                                    <label htmlFor="firstname">First Name:</label>
                                    <input type="text" id="firstname" className="form-control" />
                                </Col>
                                <Col xs={12} md={6}>
                                    <label htmlFor="lastname">Last Name:</label>
                                    <input type="text" id="lastname" className="form-control" />
                                </Col>

                            </Row>
                            <Row>
                                <Col xs={12} md={6}>
                                    <label htmlFor="email">Email Address:</label>
                                    <input type="email" id="email" className="form-control" />
                                </Col>
                                <Col xs={12} md={6}>
                                    <label htmlFor="lastname">Password:</label>
                                    <input type="password" id="password" className="form-control" />
                                </Col> 
                            </Row>

                            <Row>                                
                                <Col xs={12} md={6}>
                                    <label htmlFor="password">Confirm Password:</label>
                                    <input type="password" id="cfpassword" className="form-control" />
                                </Col>
                                <Col xs={12} md={6}>
                                    <label htmlFor="timeArrival">Estimated Time Of Arrival:</label>
                                    <input type="time" id="timeArrival" placeholder='10:00AM' className="form-control" />
                                </Col>
                            </Row>

                            <Row>
                                <Col xs={12} md={12} className='mb-5'>
                                    <label htmlFor="booking"><b>Who are you booking for?</b></label><br />
                                    <label htmlFor="bookingfor1"><input type="radio" name='booking' id="bookingfor1" /> &nbsp; I'm booking for myself</label> &nbsp; &nbsp;
                                    <label htmlFor="bookingfor2"><input type="radio" name='booking' id="bookingfor2" /> &nbsp; I'm booking for someone else</label>
                                </Col>
                                <Col xs={12} md={6}>
                                    <h4>Your price summary:</h4>
                                    <p>Original price: NGN 48,343.19</p>
                                    <p>This service is offering a 20% discount:  - NGN 9,668.64</p>
                                </Col>
                                <Col xs={12} md={6}>
                                    <h4>Price information </h4>
                                    <p>
                                        Includes NGN 5,044.51 in taxes and fees<br />
                                        5 % VAT: NGN 1,681.50<br />
                                    </p>
                                </Col>
                                <Col xs={12} md={6}>
                                    <p className='highlight'>
                                        This reservation fee is 100% refundable if you’re not satisfied with the 
                                        services booked, the owner will not get your money.
                                    </p>
                                </Col>
                                <Col xs={12} md={6}>
                                    <div className="paybox">
                                        <h4 className="price">
                                            NGN48,343.00
                                        </h4>
                                        <Link to="/pay-now">
                                            <div className="btnMore">Pay Now <PaymentOutlined /></div>
                                        </Link>
                                    </div>
                                </Col>

                            </Row>
                            
                        </form>
                        
                    </Row>
                </Col>
            </Row>
        </Container>
    </div>
  )
}

export default TourBooking