// export default Navbar

/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState } from "react";
// import Logo from "../Assets/Logo.svg";
import './Navbar.css'; 
import { HiOutlineBars3 } from "react-icons/hi2";
import Box from "@mui/material/Box";
import Drawer from "@mui/material/Drawer";
import List from "@mui/material/List";
import Divider from "@mui/material/Divider";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import HomeIcon from "@mui/icons-material/Home";
import LocalTaxi from "@mui/icons-material/LocalTaxi";  
import { Link } from 'react-router-dom'
import { FlightTakeoffOutlined, Person3TwoTone, Phone, PhoneBluetoothSpeaker, PhoneEnabledRounded, RoomServiceTwoTone, TourTwoTone } from "@mui/icons-material";

const NavbarMenu = () => {
  const [openMenu, setOpenMenu] = useState(false);
  const menuOptions = [
    {
      text: "Accommodations",
      icon: <HomeIcon />,
    },
    {
      text: "Cars",
      icon: <LocalTaxi />,
    },
    {
      text: "Flights",
      icon: <FlightTakeoffOutlined />,
    },
    {
      text: "Tours",
      icon: <TourTwoTone />,
    }, 
    {
      text: "Services",
      icon: <RoomServiceTwoTone />,
    },
    {
      text: "Companion",
      icon: <Person3TwoTone />,
    }, 
  ];
  return (
    <div className="header-wrap"> 
      <nav className="container">
          <div style={{display: 'block', margin: '20px 0'}}>
            <div className="nav-logo-container">
              {/* <img src={Logo} alt="" /> */}
              <p className='logoText'><Link to="/" style={{textDecoration: 'none', color: '#fff'}}>Bookanow<span className='logoSpan'>.com</span></Link></p>
            </div>            
            <div className="navbar-call">
              <p><Phone /> +234 8010 000 0000</p>
            </div>
            <div>

          </div> 
          <div className="navbar-links-container">
            <Link to="/accommodations"><HomeIcon style={{fontSize: "1rem"}} /> Accommodations</Link>
            <Link to="/cars"><LocalTaxi style={{fontSize: "1rem"}} /> Cars</Link>
            <Link to="/flights"><FlightTakeoffOutlined style={{fontSize: "1rem"}} /> Flights</Link>
            <Link to="/tours"><TourTwoTone style={{fontSize: "1rem"}} /> Tours</Link>
            <Link to="/services"><RoomServiceTwoTone style={{fontSize: "1rem"}} /> Services</Link>
            <Link to="/companion"><Person3TwoTone style={{fontSize: "1rem"}} /> Companion</Link>
            
            <Link to="/login"><button className="primary-button" style={{marginLeft: '275px', float: 'right'}}>Login/Register</button> </Link>
            
          </div>              
        </div>

        <div className="navbar-menu-container">
          <HiOutlineBars3 onClick={() => setOpenMenu(true)} style={{color: '#fff'}}/>
        </div>
        <Drawer open={openMenu} onClose={() => setOpenMenu(false)} anchor="right">
          <Box
            sx={{ width: 250 }}
            role="presentation"
            onClick={() => setOpenMenu(false)}
            onKeyDown={() => setOpenMenu(false)}            
          >
            <List>
              {menuOptions.map((item) => (
                <ListItem key={item.text} disablePadding>
                  <Link to={"/"+item.text.toLowerCase()} style={{color:'#333', textDecoration: 'none', fontSize: '14px'}}>
                    <ListItemButton>                    
                      <ListItemIcon>{item.icon}</ListItemIcon>
                      <ListItemText primary={item.text} />                    
                    </ListItemButton>
                  </Link>
                </ListItem>
              ))}
            </List>
            <Divider />
            <Link to="/login"><button className="primary-button" style={{marginLeft: '20px'}}>Login/Register</button></Link>
          </Box>
        </Drawer>
      </nav>
    </div>
  );
};

export default NavbarMenu;