//import logo from './logo.svg';
import './App.css';
import Homepage from './Components/Homepage';
import NavbarMenu from './Components/Layouts/Navbar'; 
import Footer from './Components/Layouts/Footer';
import 'bootstrap/dist/css/bootstrap.min.css';
import Login from './Components/Auth/Login';
import LoginStep2 from './Components/Auth/LoginStep2';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom"; 
import Accommodations from './Components/Accommodations';
import AccomPreview from './Components/AccomPreview';
import AccomBooking from './Components/AccomBooking';
import PaymentPage from './Components/PaymentPage';
import BookingSuccess from './Components/BookingSuccess';
import ServicesPage from './Components/ServicesPage';
import ServicePreview from './Components/ServicePreview';
import ServiceBooking from './Components/ServiceBooking';
import ToursPage from './Components/Tours/ToursPage';
import TourPreview from './Components/Tours/TourPreview';
import TourBooking from './Components/Tours/TourBooking';
import UserDashboard from './Components/UserAccount/UserDashboard';
import Ewallet from './Components/UserAccount/Ewallet';
import MyBookings from './Components/UserAccount/MyBookings';
import Refunds from './Components/UserAccount/Refunds';
import ApplyForRefund from './Components/UserAccount/ApplyForRefund';
import RefundThanks from './Components/UserAccount/RefundThanks';
import CarListing from './Components/Cars/CarListing';
import CarPreview from './Components/Cars/CarPreview';
import CarBooking from './Components/Cars/CarBooking';
import FlightsPage from './Components/Flights/FlightsPage';
import FlightsListing from './Components/Flights/FlightsListing';
import FlightBooking from './Components/Flights/FlightBooking';

function App() {
  return (
    <div className='wrap'>   
    <Router>
      <NavbarMenu  style={{backgroundColor: '#003B95' }} />

      <Switch>
          <Route exact path="/">
            <Homepage />
          </Route>
          
          <Route path="/login">
            <Login />
          </Route>
          <Route path="/login-step-2">
            <LoginStep2 />
          </Route>

          <Route path="/accommodations">
            <Accommodations />
          </Route>
          <Route path="/accom-preview/:id">
            <AccomPreview />
          </Route>
          <Route path="/accom-booking/:id">
            <AccomBooking />
          </Route>

          <Route path="/flights">
            <FlightsPage />
          </Route>
          <Route path="/flight-search">
            <FlightsListing />
          </Route>
          <Route path="/flight-booking">
            <FlightBooking />
          </Route>

          <Route path="/services">
            <ServicesPage />
          </Route>
          <Route path="/service-preview/:id">
            <ServicePreview />
          </Route>
          <Route path="/service-booking/:id">
            <ServiceBooking />
          </Route>

          <Route path="/tours">
            <ToursPage />
          </Route>
          <Route path="/tour-preview/:id">
            <TourPreview />
          </Route>
          <Route path="/tour-booking/:id">
            <TourBooking />
          </Route>

          <Route path="/cars">
            <CarListing />
          </Route>
          <Route path="/car-preview/:id">
            <CarPreview />
          </Route>
          <Route path="/car-booking/:id">
            <CarBooking />
          </Route>

          <Route path="/pay-now">
            <PaymentPage />
          </Route>
          <Route path="/booking-success">
            <BookingSuccess />
          </Route>

          <Route path="/user-dashboard">
            <UserDashboard />
          </Route>
          <Route path="/ewallet">
            <Ewallet />
          </Route>
          <Route path="/my-bookings">
            <MyBookings />
          </Route>
          <Route path="/request-refund">
            <Refunds />
          </Route>
          <Route path="/apply-refund">
            <ApplyForRefund />
          </Route>
          <Route path="/refund-thanks">
            <RefundThanks />
          </Route>
      </Switch>
      
      <Footer />
    </Router>   
      
    </div>
  );
}

export default App;
