import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import './UserDashboard.css'; 
import { PaymentOutlined, Payments } from '@mui/icons-material'; 
import UserNavbar from './UserNavbar';

const UserDashboard = () => {
  return (
    <div className='UserDashboard'>
        {/* Do a mini navbar just below the main navbar */}
        <Container className='container'>
            <Row>
                <Col xs={12} md={3} lg={3}>
                    <UserNavbar />
                </Col>
                <Col xs={12} md={9} lg={9}>
                    <Row>
                        <Col xs={12} md={12} className='mt-5 mb-5'>
                            <div className="welcome-panel">
                                <h3 className='mb-3'>
                                    Hi Daniel,
                                </h3>
                                <p>
                                    Welcome to Bookanow, We’re Africa’s leading hospitality and rental services company, 
                                    providing secured transaction between service providers and their customers.
                                </p>
                                <p>
                                    Save money on your next booking or trip by paying through your wallet, pay securely.
                                </p>

                            </div>
                        </Col>
                        <Col xs={12} md={6} className='mb-5'>
                            <div className="ewallet-panel">
                                <p>Wallet Balance:</p>
                                <h3 className='mt-3'>NGN 120,000.00</h3> 
                                <div className="btn btn-warning"> <Payments /> Add Funds</div>
                            </div>
                        </Col>
                        <Col xs={12} md={6} className='mb-5'>
                            <div className="ewallet-panel">
                                <h4>All your bookings:</h4>
                                <p className='mt-3'>Find all your booking history here. </p>
                                <div className="btn btn-primary"><PaymentOutlined /> View History</div>
                            </div>
                        </Col>
                    </Row>
                </Col>
            </Row>
        </Container>

    </div>
  )
}

export default UserDashboard