import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import './MyBookings.css'; 
import UserNavbar from './UserNavbar';
import { Table, Badge } from 'react-bootstrap';
import { FaEye } from 'react-icons/fa';
import { Close, RemoveCircleOutline } from '@mui/icons-material';

const MyBookings = () => {
  return (
    <div className='MyBookings'>
        <Container>
            <Row>
            <Col xs={12} md={3} lg={3}>
                    <UserNavbar />
                </Col>
                <Col xs={12} md={9} lg={9}>
                    <Row>
                        <Col xs={12} md={12} className='mt-5 mb-3'>
                            <h2>My Bookings &amp; Trips </h2>
                            <p>Find All your bookings and trips in one place. </p>

                        </Col>

                        <Col xs={12} md={12}>
                            <div className="table-panel">
                                <Table responsive>
                                    <thead>
                                        <tr>
                                            <th> # </th>
                                            <th> Reservations </th>
                                            <th> Category </th>
                                            <th> Duration </th>
                                            <th> Status </th>
                                            <th> Amount </th>
                                            <th> Action </th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr>
                                            <td>1.</td>
                                            <td>Zubiano Multiplex Apartment (Two-bedroom) </td>
                                            <td>Accomodation</td>
                                            <td>From: 28/05/2023 <br/>To: 01/06/2023</td>
                                            <td><Badge bg='warning'>Pending</Badge></td>
                                            <td><div className="text-primary">NGN48,343.00</div></td>
                                            <td width={120}>
                                                <div className="btn btn-primary"><FaEye /> </div> &nbsp;
                                                <div className="btn btn-danger"><RemoveCircleOutline /> </div>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>2.</td>
                                            <td>Flat Egea Petrol Manual - Car101  </td>
                                            <td>Car Rentals</td>
                                            <td>From: 28/05/2023 <br/>To: 01/06/2023</td>
                                            <td><Badge bg='danger'>Canceled</Badge></td>
                                            <td><div className="text-primary">NGN148,343.00</div></td>
                                            <td width={120}>
                                                <div className="btn btn-primary"><FaEye /> </div> &nbsp;
                                                <div className="btn btn-danger"><RemoveCircleOutline /> </div>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>3.</td>
                                            <td>Abuja (ABV) - Calabar (CBQ) Two-way Trips </td>
                                            <td>Flight Tickets</td>
                                            <td>From: 18/05/2023 <br/>To: 01/06/2023</td>
                                            <td><Badge bg='primary'>Completed</Badge></td>
                                            <td><div className="text-primary">NGN88,343.00</div></td>
                                            <td width={120}>
                                                <div className="btn btn-primary"><FaEye /> </div> &nbsp;
                                                <div className="btn btn-danger"><RemoveCircleOutline /> </div>
                                            </td>
                                        </tr>
                                    </tbody>
                                </Table>

                            </div>
                        </Col>

                    </Row>
                </Col>
            </Row>
        </Container>
    </div>
  )
}

export default MyBookings