import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import './PaymentPage.css';
import { PaymentOutlined } from '@mui/icons-material';
import { FaCheckCircle } from 'react-icons/fa';

const PaymentPage = () => {
  return (
    <div className='PaymentPage'>
        <Container className='container'>
            <Col xs="12" md={6} className='payment-panel'>
                <Row>
                    <Col xs={12} md={12}>
                        <h2><PaymentOutlined /> Payment Details</h2>
                    </Col>
                    <Col xs={12} md={12}>
                        <div className="card-img">

                        </div>
                    </Col>
                    <Col xs={12} md={12}>
                        <form action='./booking-success'>
                            <input type="text" name="cardHolderName" id="cardHolderName" placeholder='Card Holder Name' className="form-control mt-3" />
                            <input type="text" name="cardNumber" id="cardNumber" placeholder='Card Number' className="form-control mt-3" />
                            <Row>                                
                                <Col xs={12} md={6}>
                                    <input type="date" name="expiryDate" id="expiryDate" placeholder='Expiry Date' className="form-control mt-3" />
                                </Col>
                                <Col xs={12} md={6}>
                                    <input type="number" name="cvvNumber" id="cvvNumber" placeholder='CVV Number' className="form-control mt-3" />
                                </Col>
                            </Row>
                            <button className="btnMore mt-5 mb-3">Pay <FaCheckCircle /></button>
                        </form>
                    </Col>
                </Row>
            
            </Col>
        </Container>
    </div>
  )
}

export default PaymentPage