import React, {useState} from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { SearchRounded, TrendingUp } from '@mui/icons-material';
import './Accommodations.css'
import apImg from '../Assets/Img/apartment-101.jpg'; 
import Apartments from './Apartments';

const Accommodations = () => {
    const [countRoom, setCountRoom] = useState(1);
    const [countAdult, setAdultCount] = useState(0);
    const [countChildren, setChildrenCount] = useState(0);
  
    const subtractRoom = () => {
      if(countRoom === 0){
        setCountRoom(countRoom + 1);
      } else {
        setCountRoom(countRoom - 1);
      }
    }
  
    const addRoom = () => {
      setCountRoom(countRoom + 1);
    }
  
    const handleRoomCount = (e) => {
      setCountRoom(e.target.value);
    }
  
    const subtractAdult = () => {
      if(countAdult === 0){
        setAdultCount(countAdult + 1);
      } else {
        setAdultCount(countAdult - 1);
      }
    }
  
    const addAdult = () => {
      setAdultCount(countAdult + 1);
    }
  
    const handleAdultCount = (e) => {
      setAdultCount(e.target.value);
    }
  
    const subChildren = () => {
      if(countChildren === 0){
        setChildrenCount(countChildren + 1);
      } else {
        setChildrenCount(countChildren - 1);
      }
    }
  
    const addChildren = () => {
      setChildrenCount(countChildren + 1);
    }
  
    const handleChildrenCount = (e) => {
      setChildrenCount(e.target.value);
    }
  
    
  return (
    <div className='Accommodations'>
        <Container> 
            <Row>                
                <Col xs={12} md={4}>
                    <h2>Accommodation Deals <TrendingUp /></h2>
                    <div className="search-panel">
                        <p>WHAT ARE YOU TRAVELLING FOR?</p>
                        <form action="/accommodations">
                            <label htmlFor="travelPurpose"><input type="checkbox" name="travelPurpose" id="travelPurpose" /> Business {" "}</label> {" "}
                            <label htmlFor="travelPurpose">{" "}<input type="checkbox" name="travelPurpose" id="travelPurpose" /> Leisure</label>
                            <br></br>

                            <Row>
                                <Col xs={12} md={12}>
                                    <label htmlFor="destination">Destination:</label>
                                    <input type="text" placeholder='Where?' className='form-control mb-3' />
                                </Col>
                                <Col xs={12} md={12}>
                                    <label htmlFor="destination">Category:</label>
                                    <select className='form-select mb-3'>
                                        <option value={'Apartments'}>Apartments</option>
                                        <option value={'Hotels'}>Hotels</option>
                                        <option value={'GuestHouses'}>Guest Houses</option>
                                        <option value={'Villas'}>Villas</option>
                                        <option value={'Other'}>Other</option>
                                    </select>
                                </Col>
                                <Col xs={12} md={12}>
                                    <label htmlFor="checkin">Check-in Date:</label>
                                    <input type="date" placeholder='From?' className='form-control mb-3' />
                                </Col>
                                <Col xs={12} md={12}>
                                    <label htmlFor="checkout">Check-out Date:</label>
                                    <input type="date" placeholder='to?' className='form-control mb-3' />
                                </Col>
                                <Col xs={12} md={12}>
                                    <div className="number_ct">
                                        <label htmlFor="rooms">Room(s)</label>
                                        <div className="number">
                                        <span className="minus" onClick={subtractRoom}>-</span>
                                        <input type="text" value={countRoom} onChange={handleRoomCount} />
                                        <span className="plus" onClick={addRoom}>+</span>
                                        </div>
                                    </div>
                                </Col>
                                <Col xs={12} md={12}>
                                    <div className="number_ct">
                                        <label htmlFor="guests">Guest(s)&nbsp;</label>
                                        <div className="number">
                                        <span className="minus" onClick={subtractAdult}>-</span>
                                        <input type="text" value={countAdult} onChange={handleAdultCount} />
                                        <span className="plus" onClick={addAdult}>+</span>
                                        </div>
                                    </div>
                                </Col>
                                <Col xs={12} md={12}>
                                    <div className="number_ct">
                                        <label htmlFor="children">Children</label>
                                        <div className="number">
                                        <span className="minus" onClick={subChildren}>-</span>
                                        <input type="text" value={countChildren} onChange={handleChildrenCount} />
                                        <span className="plus" onClick={addChildren}>+</span>
                                        </div>
                                    </div>
                                </Col>
                                <Col xs={12} md={12}> 
                                    <button placeholder='Where?' className='btn btn-primary' style={{marginTop: "40px"}}>Find Deals <SearchRounded /></button>
                                </Col>
                            </Row>
                        </form>
                    </div>
                </Col>
                <Col xs={12} md={8}>
                    <Row>
                        <Col xs={12} md={6} lg={4}>
                            <Apartments
                                src={apImg}lg
                                alt= 'Zubiano house'
                                location='Abuja/Nigeria'
                                price='N25,000/night'
                                caption= 'Zubiano House' />
                        </Col>
                        <Col xs={12} md={6} lg={4}>
                            <Apartments
                                src={apImg}
                                alt= 'Zubiano house'
                                location='Abuja/Nigeria'
                                price='N25,000/night'
                                caption= 'Zubiano House' />
                        </Col>
                        <Col xs={12} md={6} lg={4}>
                            <Apartments
                                src={apImg}
                                alt= 'Zubiano house'
                                location='Abuja/Nigeria'
                                price='N25,000/night'
                                caption= 'Zubiano House' />
                        </Col>
                        <Col xs={12} md={6} lg={4}>
                            <Apartments
                                src={apImg}
                                alt= 'Zubiano house'
                                location='Abuja/Nigeria'
                                price='N25,000/night'
                                caption= 'Zubiano House' />
                        </Col>
                        <Col xs={12} md={6} lg={4}>
                            <Apartments
                                src={apImg}
                                alt= 'Zubiano house'
                                location='Abuja/Nigeria'
                                price='N25,000/night'
                                caption= 'Zubiano House' />
                        </Col>
                        <Col xs={12} md={6} lg={4}>
                            <Apartments
                                src={apImg}
                                alt= 'Zubiano house'
                                location='Abuja/Nigeria'
                                price='N25,000/night'
                                caption= 'Zubiano House' />
                        </Col>
                        <Col xs={12} md={6} lg={4}>
                            <Apartments
                                src={apImg}
                                alt= 'Zubiano house'
                                location='Abuja/Nigeria'
                                price='N25,000/night'
                                caption= 'Zubiano House' />
                        </Col>
                        <Col xs={12} md={6} lg={4}>
                            <Apartments
                                src={apImg}
                                alt= 'Zubiano house'
                                location='Abuja/Nigeria'
                                price='N25,000/night'
                                caption= 'Zubiano House' />
                        </Col>
                    </Row>
                </Col>
            </Row>
        </Container>
    </div>
  )
}

export default Accommodations