import React, {useState} from 'react'
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { SearchRounded, TrendingUp } from '@mui/icons-material';
import './ServicesPage.css'
import spaImg from '../Assets/Img/spaImg.png'; 
import faceTreatment from '../Assets/Img/faceTreatment.png'; 
import Cosmetics from '../Assets/Img/Cosmetics.png'; 
import Pedicure from '../Assets/Img/pedicure.png'; 
import DrewGym from '../Assets/Img/DrewGym.png'; 
import Services from './Services';

const ServicesPage = () => { 
    const [countAdult, setAdultCount] = useState(0);
    const [countChildren, setChildrenCount] = useState(0);
  
     
  
    const subtractAdult = () => {
      if(countAdult === 0){
        setAdultCount(countAdult + 1);
      } else {
        setAdultCount(countAdult - 1);
      }
    }
  
    const addAdult = () => {
      setAdultCount(countAdult + 1);
    }
  
    const handleAdultCount = (e) => {
      setAdultCount(e.target.value);
    }
  
    const subChildren = () => {
      if(countChildren === 0){
        setChildrenCount(countChildren + 1);
      } else {
        setChildrenCount(countChildren - 1);
      }
    }
  
    const addChildren = () => {
      setChildrenCount(countChildren + 1);
    }
  
    const handleChildrenCount = (e) => {
      setChildrenCount(e.target.value);
    }
  
    
  return (
    <div className='ServicesPage'>
        <Container> 
            <Row>                
                <Col xs={12} md={4}>
                    <h2>Other Services <TrendingUp /></h2>
                    <div className="search-panel">
                        <p>WHAT ARE YOU TRAVELLING FOR?</p>
                        <form action="/ServicesPage">
                            <label htmlFor="travelPurpose"><input type="checkbox" name="travelPurpose" id="travelPurpose" /> Business {" "}</label> {" "}
                            <label htmlFor="travelPurpose">{" "}<input type="checkbox" name="travelPurpose" id="travelPurpose" /> Leisure</label>
                            <br></br>

                            <Row>
                                <Col xs={12} md={12}>
                                    <label htmlFor="location">Location:</label>
                                    <input type="text" placeholder='Where?' className='form-control mb-3' />
                                </Col>
                                <Col xs={12} md={12}>
                                    <label htmlFor="category">Category:</label>
                                    <select className='form-select mb-3'>
                                        <option value={'SPA'}>SPA</option>
                                        <option value={'Photography'}>Photography</option>
                                        <option value={'Gym Trainer'}>Gym Trainer</option>
                                        <option value={'manicure'}>Manicure</option>
                                        <option value={'Other'}>Other Services</option>
                                    </select>
                                </Col>
                                <Col xs={12} md={12}>
                                    <label htmlFor="checkin">Check-in Date:</label>
                                    <input type="date" placeholder='From?' className='form-control mb-3' />
                                </Col>
                                <Col xs={12} md={12}>
                                    <label htmlFor="checkout">Check-out Date:</label>
                                    <input type="date" placeholder='to?' className='form-control mb-3' />
                                </Col>
                                 
                                <Col xs={12} md={12}>
                                    <div className="number_ct">
                                        <label htmlFor="guests">Adult(s)&nbsp;</label>
                                        <div className="number">
                                        <span className="minus" onClick={subtractAdult}>-</span>
                                        <input type="text" value={countAdult} onChange={handleAdultCount} />
                                        <span className="plus" onClick={addAdult}>+</span>
                                        </div>
                                    </div>
                                </Col>
                                <Col xs={12} md={12}>
                                    <div className="number_ct">
                                        <label htmlFor="children">Children</label>
                                        <div className="number">
                                        <span className="minus" onClick={subChildren}>-</span>
                                        <input type="text" value={countChildren} onChange={handleChildrenCount} />
                                        <span className="plus" onClick={addChildren}>+</span>
                                        </div>
                                    </div>
                                </Col>
                                <Col xs={12} md={12}> 
                                    <button placeholder='Where?' className='btnMore' style={{marginTop: "40px"}}>Find Deals <SearchRounded /></button>
                                </Col>
                            </Row>
                        </form>
                    </div>
                </Col>
                <Col xs={12} md={8}>
                    <Row>
                        <Col xs={12} md={6} lg={4}>
                            <Services
                                src={spaImg}
                                alt= 'SPA - Body Massage'
                                location='Lagos/Nigeria'
                                price='N25,000/hour'
                                caption= 'SPA - Body Massage'
                                id={1}  />
                        </Col>
                        <Col xs={12} md={6} lg={4}>
                            <Services
                                src={faceTreatment}
                                alt= 'Face Treatment'
                                location='Abuja/Nigeria'
                                price='N65,000/day'
                                caption= 'Face Treatment'
                                id={1}  />
                        </Col>
                        <Col xs={12} md={6} lg={4}>
                            <Services
                                src={Cosmetics}
                                alt= 'Cosmetologist'
                                location='Spain'
                                price='N25,000/night'
                                caption= 'Cosmetologist'
                                id={1}  />
                        </Col>
                        <Col xs={12} md={6} lg={4}>
                            <Services
                                src={Pedicure}
                                alt= 'Manicure & Pedicure'
                                location='Cairo/Egypt'
                                price='N25,000/night'
                                caption= 'Manicure & Pedicure'
                                id={1}  />
                        </Col>
                        <Col xs={12} md={6} lg={4}>
                            <Services
                                src={DrewGym}
                                alt= 'Drew Harris Gym'
                                location='Abuja/Nigeria'
                                price='N25,000/night'
                                caption= 'Drew Harris Gym'
                                id={1}  />
                        </Col>
                        <Col xs={12} md={6} lg={4}>
                            <Services
                                src={spaImg}
                                alt= 'Zubiano house'
                                location='Abuja/Nigeria'
                                price='N25,000/night'
                                caption= 'Zubiano House'
                                id={1}  />
                        </Col>
                        <Col xs={12} md={6} lg={4}>
                            <Services
                                src={spaImg}
                                alt= 'Zubiano house'
                                location='Abuja/Nigeria'
                                price='N25,000/night'
                                caption= 'Zubiano House'
                                id={1}  />
                        </Col>
                        <Col xs={12} md={6} lg={4}>
                            <Services
                                src={spaImg}
                                alt= 'Zubiano house'
                                location='Abuja/Nigeria'
                                price='N25,000/night'
                                caption= 'Zubiano House'
                                id={1} />
                        </Col>
                    </Row>
                </Col>
            </Row>
        </Container>
    </div>
  )
}

export default ServicesPage