import React from 'react'
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import './ServicePreview.css';
import SpaImg2 from '../Assets/Img/SpaImg2.jpg';
import landlordImg from '../Assets/Img/landlordImg.png';
import { BookOnlineOutlined, CancelOutlined, PaymentOutlined, Person2TwoTone, PetsOutlined, ShareSharp } from '@mui/icons-material'; 
import { FaHeart, FaSignInAlt, FaSignOutAlt } from 'react-icons/fa';
import { Link } from 'react-router-dom/cjs/react-router-dom';

const ServicePreview = () => {
     
    
  return (
    <div className='ServicePreview'>
        <Container className='container'>
            <Row>
                <Col xs={12} md={8}>
                    <h2>Face Treatment - Jerry Luxury SPA</h2>
                    <p>Cairo, Egypt</p>
                </Col>
                <Col xs={12} md={4}>
                    <span><ShareSharp /> Share &nbsp;&nbsp;</span>
                    <span><FaHeart /> Save</span>
                </Col>
            </Row>
            
            <Row>
                <Col xs={12} md={6} className='mt-5'>
                    <Row>
                        <Col xs={12} md={12} className='mt-3'>
                            <img src={SpaImg2} alt="Apartment" className='main-img img-fluid' />
                        </Col>
                    </Row>
                </Col>
                {/* More pictures here */}
                <Col xs={12} md={6} className='mt-5 mb-5'>
                    <Row>
                        <Col xs={12} md={6}  className='mt-3'>
                            <img src={SpaImg2} alt="Apartment" className='img-fluid'/>
                        </Col>
                        <Col xs={12} md={6} className='mt-3'>
                            <img src={SpaImg2} alt="Apartment" className='third-img img-fluid' />
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={12} md={6} className='mt-3'>
                            <img src={SpaImg2} alt="Apartment" className='img-fluid' />
                        </Col>
                        <Col xs={12} md={6} className='mt-3'>
                            <div className="featBoxImg">
                                <img src={SpaImg2} alt="Apartment" className='fourth-img img-fluid' />
                                <div className='seeMore'>
                                    See all photos
                                </div>
                            </div>
                        </Col>
                    </Row>
                </Col>
            </Row>
              
            <Row>
                <Col xs={12} md={12} className='rules mb-5'>
                    <Row>
                        <Col xs={12} md={12} className='mb-5'>
                            <Row>
                                <Col xs={8} md={6}>
                                    <h3><span className='rental-property'>SPA - Body Massage </span> Services </h3>
                                </Col>
                                <Col xs={4} md={6}> 
                                    <h3>
                                        Hosted by <span className='landlord' style={{marginRight: ''}}>Jerry Curter {" "}</span>
                                        &nbsp; <img src={landlordImg} className='landlordImg' alt="landlord-img" />
                                    </h3> 
                                </Col>
                            </Row>
                        </Col>
                         
                        <Col xs={12} md={12}>
                            <h4>About This Place</h4>
                            <p>
                                Located in La Juarez, the neighborhood is less touristy, with great places to eat and a 5 minute walk from 
                                Roma Norte or Reforma. With two floors of windows, balcony and roof top terrace, this place is full of light 
                                and plants, a great place to work from home, unwind and enjoy a drink a sunset.
                            </p>
                        </Col> 

                        <Col xs={12} md={12}>
                            <h4>Service Rules</h4>
                        </Col>
                        <Col xs={12} md={6}>
                            <span><FaSignInAlt/> Check In</span>
                        </Col>
                        <Col xs={12} md={6}>
                            <p>From 12:00 PM to 2:00 PM</p>
                        </Col>
                        <Col xs={12} md={6}>
                            <span><FaSignOutAlt /> Check Out</span>
                        </Col>
                        <Col xs={12} md={6}>
                            <p>From 11:00 AM to 12:00 PM</p>
                        </Col> 
                        <Col xs={12} md={6}>
                            <span><CancelOutlined /> Cancellation/prepayment</span>
                        </Col>
                        <Col xs={12} md={6}>
                            <p>
                                Cancellation and prepayment policies vary according to accommodations type. 
                                Check what conditions might apply to each option when making your selection.
                            </p>
                        </Col> 
                        <Col xs={12} md={6}>
                            <span><Person2TwoTone /> No age restriction</span>
                        </Col>
                        <Col xs={12} md={6}>
                            <p>
                                There's no age requirement for check-in
                            </p>
                        </Col>
                        <Col xs={12} md={6}>
                            <span><PetsOutlined /> Pets</span>
                        </Col>
                        <Col xs={12} md={6}>
                            <p>
                                Pets are not allowed.
                            </p>
                        </Col>
                        <Col xs={12} md={6}>
                            <span><PaymentOutlined /> Accepted payment methods</span>
                        </Col>
                        <Col xs={12} md={6}>
                            <p>
                                Cash is not accepted, <b>Pay securely through your e-wallet.</b>
                            </p>
                        </Col>
                        
                        <Col xs={12} md={12}>                                            
                            <Link to="/service-booking/1">
                                <div className="btnMore">
                                    Book Now <BookOnlineOutlined/>
                                </div>
                            </Link>
                        </Col>
                    </Row>
                </Col>
                
            </Row>
        </Container>
    </div>
  )
}

export default ServicePreview