import React from 'react'
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import './CarPreview.css';
import CarImg from '../../Assets/Img/car101.jpg';
import landlordImg from '../../Assets/Img/landlordImg.png';
import { AirOutlined, BathroomOutlined, BedOutlined, BookOnlineOutlined, CancelOutlined, CarCrashOutlined, ChairAltOutlined, CheckCircle, ChildCareTwoTone, DoorSlidingOutlined, EngineeringOutlined, FitnessCenterOutlined, GasMeterOutlined, HouseOutlined, PaymentOutlined, Person2TwoTone, PetsOutlined, PowerInputOutlined, RoomServiceOutlined, ShareSharp } from '@mui/icons-material'; 
import { FaGasPump, FaHeart, FaParking, FaSignInAlt, FaSignOutAlt, FaSmokingBan, FaSwimmingPool, FaTaxi, FaWifi } from 'react-icons/fa';
import { Link } from 'react-router-dom/cjs/react-router-dom';
import { HiUsers } from 'react-icons/hi2';

const CarPreview = () => {
     
    
  return (
    <div className='CarPreview'>
        <Container className='container'>
            <Row>
                <Col xs={12} md={8}>
                    <h2>Ford Edge (2013) </h2>
                    <p>Lokogoma, Abuja</p>
                </Col>
                <Col xs={12} md={4}>
                    <span><ShareSharp /> Share &nbsp;&nbsp;</span>
                    <span><FaHeart /> Save</span>
                </Col>
            </Row>
            
            <Row>
                <Col xs={12} md={6} className='mt-5'>
                    <Row>
                        <Col xs={12} md={12} className='mt-3'>
                            <img src={CarImg} alt="car" className='main-img img-fluid' />
                        </Col>
                    </Row>
                </Col>
                {/* More pictures here */}
                <Col xs={12} md={6} className='mt-5 mb-5'>
                    <Row>
                        <Col xs={12} md={6}  className='mt-3'>
                            <img src={CarImg} alt="car" className='img-fluid'/>
                        </Col>
                        <Col xs={12} md={6} className='mt-3'>
                            <img src={CarImg} alt="car" className='third-img img-fluid' />
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={12} md={6} className='mt-3'>
                            <img src={CarImg} alt="car" className='img-fluid' />
                        </Col>
                        <Col xs={12} md={6} className='mt-3'>
                            <div className="featBoxImg">
                                <img src={CarImg} alt="car" className='fourth-img img-fluid' />
                                <div className='seeMore'>
                                    See all photos
                                </div>
                            </div>
                        </Col>
                    </Row>
                </Col>
            </Row>
              
            <Row>
                <Col xs={12} md={12} className='rules mb-5'>
                    <Row>
                        <Col xs={12} md={12} className='mb-5'>
                            <Row>
                                <Col xs={8} md={6}>
                                    <h3><span className='rental-property'>Luxury Car</span> in a rental unit </h3>
                                </Col>
                                <Col xs={4} md={6}> 
                                    <h3>
                                        Hosted by <span className='landlord' style={{marginRight: ''}}>Zubiano Motors {" "}</span>
                                        &nbsp; <img src={landlordImg} className='landlordImg' alt="landlord-img" />
                                    </h3> 
                                </Col>
                            </Row>
                        </Col>
                        <Col xs={12} md={12} className='mb-5'>
                        
                            <Row>
                                <Col xs={12} md={4}>
                                    <div className='features'>
                                        <FaGasPump /><br /> Petrol
                                    </div>
                                </Col>
                                <Col xs={12} md={4}>
                                    <div className='features'>
                                        <GasMeterOutlined /><br /> Automatic
                                    </div>
                                </Col>
                                <Col xs={12} md={4}>
                                    <div className='features'>
                                        <HiUsers /><br />
                                        Persons
                                    </div>
                                </Col>              
                                
                            </Row>

                        </Col>
                        <Col xs={12} md={12}>
                            <h4>About This Vehicle</h4>
                            <p>
                                Located in La Juarez, the neighborhood is less touristy, with great places to eat and a 5 minute walk from 
                                Roma Norte or Reforma. With two floors of windows, balcony and roof top terrace, this place is full of light 
                                and plants, a great place to work from home, unwind and enjoy a drink a sunset.
                            </p>
                        </Col>
                        <Col xs={12} md={12} className='offers'>
                            <h4>Vehicle Features</h4>
                            <span><EngineeringOutlined /> Base Engine Size: <b>(1400)</b></span>
                            <span><PowerInputOutlined /> Engine Power <b>(95)</b></span>
                            <span><FaTaxi /> Fuel Consumption <b>(5.3)</b></span>
                            <span><ChairAltOutlined /> No of seats <b>(5)</b></span>
                            <span><AirOutlined /> Air Conditioner <b>(Yes)</b></span>
                            <span><CarCrashOutlined /> Doors <b>(4)</b></span> 
                        </Col>

                        <Col xs={12} md={12}>
                            <h4>Vehicle Rules</h4>
                        </Col>
                        <Col xs={12} md={6}>
                            <span><FaSignInAlt/> Pick Up</span>
                        </Col>
                        <Col xs={12} md={6}>
                            <p>From 12:00 PM to 2:00 PM</p>
                        </Col>
                        <Col xs={12} md={6}>
                            <span><FaSignOutAlt /> Drop Off</span>
                        </Col>
                        <Col xs={12} md={6}>
                            <p>From 11:00 AM to 12:00 PM</p>
                        </Col> 
                        <Col xs={12} md={6}>
                            <span><CancelOutlined /> Cancellation/prepayment</span>
                        </Col>
                        <Col xs={12} md={6}>
                            <p>
                                Cancellation and prepayment policies vary according to vehicle type. 
                                Check what conditions might apply to each option when making your selection.
                            </p>
                        </Col>
                        <Col xs={12} md={6}>
                            <span><ChildCareTwoTone /> Baby Safety</span>
                        </Col>
                        <Col xs={12} md={6}>
                            <p>
                                Baby safety chair is not available in this vehicle.
                            </p>
                        </Col>
                        <Col xs={12} md={6}>
                            <span><Person2TwoTone /> Driver Restrictions</span>
                        </Col>
                        <Col xs={12} md={6}>
                            <p>
                                Minimum age requirement for car reservation is 25yrs and above.
                            </p>
                        </Col>
                        <Col xs={12} md={6}>
                            <span><PetsOutlined /> Pets</span>
                        </Col>
                        <Col xs={12} md={6}>
                            <p>
                                Pets are not allowed.
                            </p>
                        </Col>
                        <Col xs={12} md={6}>
                            <span><PaymentOutlined /> Accepted payment methods</span>
                        </Col>
                        <Col xs={12} md={6}>
                            <p>
                                Cash is not accepted, <b>Pay securely through your e-wallet.</b>
                            </p>
                        </Col>
                        
                        <Col xs={12} md={12}>   
                            <div className="reserve-pay">
                                <Row>
                                    <Col xs={12} md={4}>
                                        <p className='text-success'><CheckCircle /> <br /><b>Car Owner reserves right to cancel <br />if you're not eligible</b></p>
                                    </Col>
                                    <Col xs={12} md={4}>
                                        <p>Total Price includes Damage Insurance which will be refunded on Drop Off date</p>
                                        <h3 className='text-primary'>NGN125,000.00</h3>
                                    </Col>
                                    <Col xs={12} md={4}>                                       
                                        <Link to="/car-booking/1">
                                            <div className="btnMore">
                                                Book Now <BookOnlineOutlined/>
                                            </div>
                                        </Link>
                                    </Col>

                                </Row> 
                            </div> 
                        </Col>
                    </Row>
                </Col>
                
            </Row>
        </Container>
    </div>
  )
}

export default CarPreview