import React from 'react'
import { Link } from 'react-router-dom/cjs/react-router-dom'
import { DashboardOutlined, PaymentOutlined, TrendingUp } from '@mui/icons-material'; 
import { HiOutlineReceiptRefund, HiUser } from 'react-icons/hi2';
import { FaSignOutAlt } from 'react-icons/fa';

const UserNavbar = () => {
  return (
    <div>
        <ul className='navbar-menu'>
            <li><Link to={'./user-dashboard'}><DashboardOutlined/> Dashboard</Link></li>
            <li><Link to={'./ewallet'}><PaymentOutlined /> E-Wallet</Link></li>
            <li><Link to={'./my-bookings'}><TrendingUp /> Bookings &amp; Trips</Link></li>
            <li><Link to={'./request-refund'}><HiOutlineReceiptRefund /> Request Refund</Link></li>
            <li><Link to={'./user-profile'}><HiUser /> Manage Profile</Link></li>
            <li className='mt-5'><Link to={'#logout'}><FaSignOutAlt /> Logout</Link></li>
        </ul>
    </div>
  )
}

export default UserNavbar